import styled from '@emotion/styled'
import { memo, useEffect, useRef } from 'react';
import { useModal } from '../Hooks';
import { ModalTypes } from '../Types';
import { Div } from 'Elements';

export const Container: ModalTypes['Parts']['Container'] = memo((props) => {
  const { children } = props

  const { isOpen, closeModal } = useModal()
  const ContainerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (e: MouseEvent) => {
      if (ContainerRef.current && !ContainerRef.current.contains(e.target as Node)) {
        closeModal()
      }
    }

    if (isOpen) window.addEventListener('mousedown', handleClickOutside)
    return () => window.removeEventListener('mousedown', handleClickOutside)
  }, [isOpen, closeModal])

  return (
    <ContainerWrapper ref={ContainerRef} isOpen={isOpen} {...props}>
      {children}
    </ContainerWrapper>
  )
})

const ContainerWrapper = styled(Div)<ModalTypes['Props']['Container']>`
  visibility       : ${(props) => (props.isOpen ? 'visible' : 'hidden')};
  opacity          : ${(props) => (props.isOpen ? 1 : 0)};
  transition       : opacity ${({ isOpen }) => isOpen ? '0.3s' : '0.2s'} ease-in-out, visibility ${({ isOpen }) => isOpen ? '0.3s' : '0.2s'} ease-in-out;
  position         : fixed;
  top              : 50%;
  left             : 50%;
  transform        : translate(-50%, -50%);
  width            : 60%;
  max-width        : 500px;
  min-width        : 300px;
  background-color : #d9d9d9;
  color            : black;
  border           : 1px solid #ccc;
  box-shadow       : 10px 10px 15px rgba(0, 0, 0, 0.3);
  z-index          : 1000;
  border-radius    : 10px;
  margin           : 0 auto;
  overflow         : hidden;
`;